.popup{
    position:absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 140vh;
    background-color: rgba(0, 0, 0, 0.178);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}
.popup-inner{
    position: relative;
    padding: 32px;
    border-radius: 20px;
    width: 80%;
    height:auto ;
    max-width: 640px;
    background-color: #fff;
    border: 2px solid black;
    
}
.popup-inner:hover{
    box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}
.popup-inner .close-btn{
    position: absolute;
    top: 16px;
    right: 16px;
    background-color: white;
    border: none;
    font-size: 2rem;
    border-radius: 50%;
}
.popup-inner .close-btn:hover{
    background-color: rgba(0, 0, 0, 0.287);
}

